);\r\n      }\r\n    },\r\n    computed:{\r\n      validate:function(){\r\n        if(this.username&&this.password)\r\n          return true;\r\n        else\r\n          return false;\r\n      }\r\n    },\r\n    mounted:function(){\r\n\r\n    }\r\n  }\r\n\r\n</script>\r\n\r\n<!-- Add \"scoped\" attribute to limit CSS to this component only -->\r\n<style>\r\n\r\n  #login{\r\n    letter-spacing: 2px;\r\n    box-shadow: 0px 4px 11px 1px #d6d6d6;\r\n    display: block;\r\n    background-color: white;\r\n    max-width: 400px;\r\n    border-radius: 2px; \r\n    position: relative;\r\n    top: 50%;\r\n    transform: translate(0,-50%);\r\n    -ms-transform:translate(0,-50%);\r\n    overflow: hidden;\r\n    margin: 0 auto;\r\n  }\r\n  .login_title{\r\n    text-align: center;\r\n    margin: 0 0 21px;\r\n    padding: 20px 0;\r\n    background-color: #20A0FF;\r\n    color: white;\r\n    font-weight:bolder;\r\n  }\r\n  \r\n  .login_body{\r\n      padding: 0 20px;\r\n      margin-bottom:20px;\r\n  }\r\n  .line{\r\n    width: 100%;\r\n    height: 9px;\r\n    background-color: #5cb85c;\r\n    margin: 0 0 20px;\r\n  }\r\n\r\n  #loginBox{\r\n    height: 100%;\r\n    width: 100%;\r\n  }\r\n\r\n  .login_body{\r\n    overflow: hidden;\r\n  }\r\n  .login__input--con{\r\n     margin-bottom: 10px;\r\n  }\r\n\r\n  .login__submit{\r\n    display: block;\r\n    float: right;\r\n  }\r\n\r\n\r\n/*element ui */\r\n  .el-input__inner,.el-button{\r\n    border-radius: 2px;\r\n  } \r\n\r\n  .el-input-group__prepend{\r\n    border-top-left-radius: 2px;\r\n  border-bottom-left-radius: 2px;\r\n  }\r\n\r\n  .el-button--success{\r\n    background-color: #5cb85c;\r\n  }\r\n</style>\r\n"],"sourceRoot":"webpack://"}]);

